.advantages {
  background-color: $ghost-white;
  padding-top: 146px;
  padding-bottom: 7px;

  @media (max-width: $tablet-width) {
    padding-top: 92px;
    padding-bottom: 92px;
  }

  @media (max-width: $mobile-width) {
    padding: 62px 0 45px;
  }

  h2 {
    text-align: center;
    font-family: $alumni-sans;
    color: $sapphire;
    font-size: 54px;
    line-height: 65px;
    font-weight: 600;
    letter-spacing: 0.03em;
    margin: 0;
    margin-left: 10px;

    @media (max-width: $tablet-width) {
      font-size: 42px;
      line-height: 50px;
      margin-bottom: 0;
      margin-left: -20px;
    }

    @media (max-width: $mobile-width) {
      font-size: 32px;
      line-height: 38px;
      margin-bottom: 30px;
    }
  }
}

.advantages__enumeration {
  position: relative;
  display: grid;
  grid-template-columns: 340px 340px;
  column-gap: 362px;
  row-gap: 25px;
  grid-template-rows: 1fr 1fr;
  margin-left: 60px;
  margin-right: 60px;
  justify-content: center;

  @media (max-width: $tablet-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 280px;
    margin: 0;
  }

  @media (max-width: $mobile-width) {
    display: block;
  }
}

.advantages__gym {
  background-color: $resolution-blue;
  color: $base-white;
  padding: 50px 34px 58px;

  @media (min-width: $desktop-width) {
    min-width: 240px;
    max-width: 240px;
    top: 55px;
  }

  @media (min-width: 768px) {
    position: absolute;
    min-width: 442px;
    max-width: 442px;
    left: 50%;
    transform: translateX(-50%);
    top: 163px;
  }

  @media (max-width: $tablet-width) {
    padding: 30px 135px 37px;
  }

  @media (max-width: $mobile-width) {
    position: static;
    padding: 32px 64px 42px;
    min-width: 100%;
    margin-top: 41px;
    margin-bottom: 28px;
  }

  span {
    display: block;
    font-size: 74px;
    line-height: 81px;
    font-weight: 700;
    text-align: center;
    margin-bottom: -5px;
  }

  h3 {
    margin: 0;
    margin-bottom: 32px;
    font-size: 26px;
    line-height: 39px;
    font-weight: 700;
    text-align: center;
    margin-top: 3px;

    @media (max-width: $tablet-width) {
      font-size: 22px;
      line-height: 33px;
      margin-bottom: 15px;
      margin-top: 0;
    }

    @media (max-width: $mobile-width) {
      margin-bottom: 8px;
    }
  }

  p {
    margin: 0;
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}


.advantages__option {
  padding-top: 28px;
  padding-bottom: 76px;
  display: flex;
  gap: 30px;

  @media (max-width: $tablet-width) {
    padding: 0;
    padding-top: 22px;
  }

  @media (max-width: $mobile-width) {
    display: block;
    padding: 0;
    margin-bottom: 20px;
  }
}

.advantages__description {
  h3 {
    margin: 0;
    margin-top: 20px;
    margin-bottom: 5px;
    font-family: $roboto-condensed;
    color: $sapphire;
    font-size: 22px;
    line-height: 33px;
    font-weight: 700;

    @media (max-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 5px;
    }

    @media (max-width: $mobile-width) {
      text-align: center;
      margin-top: 5px;
      margin-bottom: 3px;
    }
  }

  p {
    color: $sapphire;
    font-size: 16px;
    line-height: 22px;
    font-weight: 300;
    margin: 0;

    @media (max-width: $mobile-width) {
      text-align: center;
      margin-left: auto;
      margin-right: auto;
      max-width: 230px;
    }
  }
}

.advantages__wrapper {
  span {
    display: block;

    @media (max-width: $mobile-width) {
      text-align: center;
    }
  }

  span:first-of-type {
    font-size: 71px;
    line-height: 107px;
    font-weight: 700;
    color: transparent;
    background-clip: text;
    background-image: linear-gradient(127.61deg, $crimson 0%, $cobalt 45.58%);

    @media (max-width: $tablet-width) {
      font-size: 58px;
      line-height: 87px;
      margin-top: 10px;
    }
  }

  span:last-of-type {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: $persian-blue;
    margin-top: -22px;

    @media (max-width: $mobile-width) {
      margin-top: -18px;
    }
  }
}

.advantages__option--aspect {
  @media (min-width: $desktop-width) {
    position: relative;

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      background-color: $crimson;
      opacity: 0.2;
      bottom: 0;
    }
  }
}

.advantages__wrapper--special span {
  @media (min-width: $desktop-width) {
    margin-left: 19px;
  }
}

.advantages__container {
  @media (min-width: $desktop-width) {
    min-width: 994px;
    max-width: 1714px;
  }

  @media (min-width: 1351px) {
    padding: 0 103px;
  }

  @media (min-width: 768px) {
    min-width: 678px;
    padding: 0 45px;
    margin: 0 auto;
  }
}
